import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbDate , NgbCalendar } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-venue-details',
  templateUrl: './venue-details.component.html',
  styleUrls: ['./venue-details.component.scss']
})
export class VenueDetailsComponent implements OnInit {
  starRating = 0;
  currentTab = 1;
  isCollapsed: boolean;
  TabContent:any;
  TabContent1:any;
  TabContent2:any;
  TabContent3:any;
  TabContent4:any;
  TabContent5:any;
  TabContent6:any;
  TabContent7:any;
  TabContent8:any;
  hoveredDate: NgbDate;
  fromNGDate: NgbDate;
  toNGDate: NgbDate;
  selected: any;
  hidden: boolean;
  public firstColleaps = false;
  public secondColleaps = false;
  public bothColleaps = false;

  constructor(private calendar: NgbCalendar,private route: ActivatedRoute) { }

  ngOnInit(): void {
    // Collapse value
    this.isCollapsed = false;
    let tab = this.route.snapshot.queryParams['tab'];
    if(tab && !isNaN(tab))
      this.currentTab = parseInt(tab)
  }

  next(tabIndex){
    debugger
    this.currentTab = tabIndex.toString();
  }
  prev(){
    this.currentTab--;
  }
  changeToCalender(){
    debugger
    document.getElementById('calnder-holder').style.height = "99%";
  }
}
